	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-11-22-top-ten-reasons-to-use-windows-vista/index.md absPath of file >>> MarkdownRemark","html":"<p>I have a mac. I love my mac. I run VMWare so that I can use Windows as needed like when I need to test Internet Explorer because apparently a lot of people out there are still using it. Who knew?</p>\n<p>At any rate I have a free copy of Windows Vista Ultimate edition that I got for attending a Microsoft developer conference. I thought I would give it a go. You should too.</p>\n<p>Here are my top ten reasons to use Windows Vista:</p>\n<ol>\n<li>Your blood pressure is to low and you are feeling adventurous</li>\n<li>You need a divorce so you install it on your wife's computer</li>\n<li>You are the IT support guy at work and you want to get fired</li>\n<li>Life has lost all meaning</li>\n<li>You really associate with that nerdy guy on the Apple commercials</li>\n<li>You have way to much free time and you like to wait around for your computer to do really hard stuff like open your email</li>\n<li>You enjoy click on I agree, OK, Yes please allow, Yes please !@#$!@ allow the stupid program to open, access the internet, save a file, print, ...</li>\n<li>Hey, its better than having bamboo shoots shoved under your finger nails.</li>\n<li>You are a usability engineer and you want to study how not to build the ui for an operating system</li>\n<li>You have not yet heard about the Mac.</li>\n</ol>","frontmatter":{"title":"Top Ten Reasons to Use Windows Vista","date":"November 22, 2008","imageUrl":null,"image":null}}},"pathContext":{"path":"/top-ten-reasons-to-use-windows-vista"}}

